
.authorCard {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: darkblue;
  color: white;
}

.avatar {
  width: 100%;
}

.authorCardTop {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 15px;
  background-color: #0b0ba2;
}

.authorCardBody {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
}

.authorSocialMedia {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.authorAvatarWrapper {
    display: flex;
    border-radius: 100%;
    overflow: hidden;
    height: 150px;
    align-items: center;
    justify-content: center;
    width: 150px;
}
